@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap");

html,
body {
  background-color: hsl(257, 40%, 49%);
  font-family: "Open Sans", sans-serif;
}

@media screen and (max-width: 375px) {
  html,
  body {
    background-image: url("bg-mobile.svg");
    background-repeat: no-repeat;
    background-attachment: fixed;
  }

  .logo img {
    height: 32px;
    padding: 25px;
  }

  .illustration img {
    padding-top: 20px;
    padding-bottom: 20px;
    width: 93%;
  }

  .details {
    text-align: center;
  }

  .details h1 {
    color: white;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 1.5rem;
  }

  .details p {
    color: white;
  }

  .details button {
    color: hsl(257, 40%, 49%);
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 55px;
    padding-left: 55px;
    border-radius: 9999px;
    border: none;
    cursor: pointer;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  }

  .details button:hover {
    color: white;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    background-color: hsl(300, 69%, 71%);
  }

  .company-logos {
    text-align: center;
    padding-top: 30px;
  }

  .company-logos > #logos {
    border: 2px solid white;
    width: 2rem !important;
    height: 2rem !important;
    border-radius: 9999px;
    margin-right: 10px;
    cursor: pointer;
  }
}

@media screen and (min-width: 375px) {
  html,
  body {
    background-image: url("bg-desktop.svg");
    background-attachment: fixed;
    background-repeat: no-repeat;
  }

  .logo img {
    height: 45px;
    padding: 30px;
  }

  .content {
    display: flex;
  }

  .illustration img {
    padding-top: 30px;
    padding-left: 30px;
    justify-content: start;
  }

  .details {
    padding-top: 35px;
    padding-left: 35px;
  }

  .details h1 {
    color: white;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 2.5rem;
  }

  .details p {
    color: white;
  }

  .details button {
    color: hsl(257, 40%, 49%);
    margin-top: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 55px;
    padding-left: 55px;
    border-radius: 9999px;
    border: none;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }

  .details button:hover {
    color: white;
    background-color: hsl(300, 69%, 71%);
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  }

  .company-logos {
    text-align: end;
    padding-right: 50px;
  }

  .company-logos > #logos {
    border: 2px solid white;
    width: 2rem !important;
    height: 2rem !important;
    border-radius: 9999px;
    margin-right: 10px;
    cursor: pointer;
  }
}
